export const getProductCategory = pathname => {
  let categoryName = '';
  switch (pathname) {
    case 'cakes':
      categoryName = '';
      break;
    case 'pastries':
      categoryName = '';
      break;
    case 'cookies':
      categoryName = '';
      break;
    case 'bakering':
      categoryName = '';
      break;
    case 'pies':
      categoryName = '';
      break;
    default:
      categoryName = '';
      break;
  }
  return categoryName;
};
